import config from "@/config/common.config"
import { previewCertificate } from '@/api/studyCenter'
export default {
  props: {
    data: {
      type: Object,
      required: true
    },
    prosess: { // 显示进度条
      type: Boolean,
      default: false
    },
    isOrder: { //是否属于确认订单页
      type: Boolean,
      default: false
    },
    isTops: { //是否属于推荐会议
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      config,
      url:'',
      currentTime:parseInt((new Date()).getTime()/1000)
    }
  },
  computed:{
    //判断是否优惠
    isDisCount() {
      return this.currentTime>=this.data.discountStart && this.data.discountEnd>=this.currentTime
    },
    //判断直播状态
    livingStatus() {
      if(this.data.liveStatus === 1 || ((this.currentTime > this.data.start) && (this.data.end > this.currentTime)&& this.data.type !== 3)){
        return { title: '直播中', style:'living'}
      }else if((this.data.start - this.currentTime)<900 && (this.data.start - this.currentTime)>0 && this.data.type !== 3){
        return { title: '即将开始', style:'willStart'}
      }else if((this.currentTime > this.data.end) && this.data.type === 1) {
        return { title: '已结束', style:'endLiving'}
      }
      return null
    }
  },
  methods:{
    preview({id}, type){
      this.previewCertificate({courseId:id, receiveId:type})
    },
    goCourseDetail({id}){
      this.$router.push(`/courseDetail/${id}`)
    },
    async previewCertificate(params){
      let data = await previewCertificate(params)
      let base64 = this.$transformArrayBufferToBase64(data.data)
      this.url = base64 ? 'data:image/png;base64,'+ base64 : ''
    }
  }
}
