/**学习中心 */
import request from "@/utils/request"
import qs from "qs"

// 获取学习统计
export const studyStatistics = () => {
  return request.post(`/school/study/statistics`)
}

// 最近学习列表
export const studyList = (params) => {
  return request.post(`/school/study/list`, qs.stringify(params))
}

// 我的收藏
export const studyFavoriteList = (params) => {
  return request.post(`/school/favorite/list`, qs.stringify(params))
}

//我的课程
export const getStudyCourse = (params) => {
  return request.post(`/school/study/courses`, qs.stringify(params))
}

//获取我的订单列表
export const getTradeList = (params) => {
  return request.post(`/school/student/trade/list`, qs.stringify(params))
}


//支付订单
export const payTrade = (params) => {
  return request.post(`/school/student/trade/pay/${params}`)
}

//取消订单
export const cancleTrade = (params) => {
  return request.post(`/school/student/trade/cancel/${params}`)
}

//获取我的一对一订单
export const getbiuniqueTrade = (params) => {
  return request.post(`/school/train/trade/list`, qs.stringify(params))
}

// 预览证书
export const previewCertificate = ({courseId, receiveId}) => {
  return request.post(`/school/study/cert/${courseId}/${receiveId}`, {}, {
    responseType: 'arraybuffer'
  })
}
