import ListContainer from '@/components/Common/ListContainer'
import CourseCard from '@/components/Common/CourseCard'
import { getStudyCourse } from '@/api/studyCenter'
export default {
  components: {
    ListContainer,
    CourseCard
  },
  data () {
    return {
      tabs:[{title:'单课', type:1}, {title:'系列课', type:2}, {title:'训练营', type:3}],
      activeName:1,
      listFlag: 0,
      tableData:[],
      loading: false,
      finished: false,
      pagination: {
        type:1,
        page: 1
      }
    }
  },
  mounted() {
    this.getListHand()
  },
  methods: {
    async getListHand () {
      const params = {
        type:1,
        ...this.pagination
      }
      const { data } = await getStudyCourse(params)
      this.loading = false
      if (data.code === 0) {
        this.tableData = this.tableData.concat(data.data.entries)
        if (!this.tableData.length && this.pagination.page === 1) {
          this.listFlag = 2
        } else {
          this.listFlag = 1
          this.pagination.page = this.pagination.page + 1
        }
        if (this.tableData.length >= +(data.data.total)) {
          this.finished = true
        }
      }
    },
    tabsChange(type){
      this.listFlag= 0
      this.tableData=[]
      this.loading=false
      this.finished=false
      this.pagination.type = type
      this.pagination.page = 1
      this.getListHand()
    }
  },
  head () {
    return {
      title: '我的课程—艾思云课堂'
    }
  }
}
